body {
  margin: 0;
}

.demo__container {
  padding: 1rem 0 1rem 0;
}

.demo__deck_image {
  width: 400px;
  height: 200px;
  object-fit: cover;
  border-radius: 20px;
}

.demo__deck_hover_icons {
  font-size: 40px;
  color: white;
  height: 50px;
  width: 50px;
  background-color: black;
  border-radius: 9999px;
  display: flex;
  justify-content: center;
  align-items: center;
}
